$transition: all 0.3s ease;
/* FONTS */
$fonts: (
    default: 'ProximaNova'
);
/* COLORS */
$colors: (
    default: #f00480,
    linkActive: #f00480,
    text: #000
);
/* BACKGROUNDS */
$bgColors: (
    default: #F2F2F2,
    primary: #ebe7e8
);
/* MEDIA QUERIES */
$media: (
    desktop_hd: 1700px,
    desktop: 1199px,
    tablet: 991px,
    mobile: 767px
);