.button {
    display: inline-block;
    overflow: hidden;
    transition: $transition;
    text-decoration: none;
    outline: none;
    position: relative;
    cursor: pointer;


    &-default {
        border: 0;
        border-radius: 20px;
        color: #fff;
        font-weight: 300;
        font-size: 18px;
        padding: 10px 30px 9px;
        background: #ED1680;

        &:hover {
            background: darken(#ED1680, 5%);
        }
        &:focus,
        &:active {
            background: darken(#ED1680, 10%);
        }
        @include desktop_hd {
            border-radius: 30px;
            padding: 12px 35px 11px;
            font-size: 24px;
        }
    }
}