.site-header {
    padding: 15px 0;

    &._shadow {
        position: relative;
        z-index: 100;

        &:before {
            content: '';
            position: absolute;
            top: 100%;
            left: 0;
            right: 0;
            height: 10px;
            background: url('../shadow-header.png') no-repeat;
            background-size: auto 200px;
            background-position: center 0;
        }
    }
    &._standard {
        box-shadow: 0 -13px 21px 3px rgba(#000, .5);
    }
    .right {
        margin-left: auto;
    }
    @include desktop_hd {
        padding: 45px 0;
    }
    @include tablet {
        .site-nav {
            display: none;
        }
        .site-mobile .site-nav {
            display: block;
        }
    }
}