.banner {
    padding: 80px 0;

    &._main {
        background-repeat: no-repeat;
        background-position: right center;
        background-size: 60% auto;
        min-height: calc(100vh - 88px - 90px);
        display: flex;

        .container {
            height: auto;
        }
        p {
            margin: 50px 0 0 0;

            @include media-max(1400px) {
                margin-top: 30px;
            }
        }
        img {
            max-height: 370px;

            @include media-max(1400px) {
                max-height: 200px;
            }
        }
        @include desktop_hd {
            min-height: calc(100vh - 148px - 100px);
        }
        @include media-max(1400px) {
            background-size: 50% auto;
        }
        @include tablet {
            padding-bottom: 500px;
            background-size: auto 400px;
            background-position: center calc(100% - 60px);

            .container {
                align-items: flex-start;
            }
        }
        @include mobile {
            padding-bottom: 370px;
            background-size: auto 300px;
            background-position: center calc(100% - 40px);

            p {
                margin-top: 30px;
                text-align: justify;
            }
            img {
                max-width: 60%;
            }
        }
    }
    &._about {
        min-height: 420px;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center 25%;

        @include desktop_hd {
            background-position: center 20%;
            min-height: 620px;
        }
        @include tablet {
            background-position: center 20%;
            min-height: 320px;
        }
    }
    &-content {
        max-width: 550px;

        @include tablet {
            max-width: none;
            text-align: center;
            margin: 0;
        }
    }
    @include media-max(1400px) {
        padding: 40px 0;
    }
    @include mobile {
        padding: 30px 0;
    }
}