a {
    transition: $transition;
}

p {
    font-size: 18px;
    margin: 0;
    font-weight: 300;
    line-height: 1.4;
    color: map_get($colors, text);

    &:not(:last-child) {
        margin-bottom: 20px;
    }

    @include desktop_hd {
        font-size: 24px;
        line-height: 1.3;
    }

    @include mobile {
        font-size: 16px;
        line-height: 1.3;
    }
}

a {
    color: map_get($colors, default);
    text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    color: map_get($colors, text);
}
h3 {
    font-weight: 400;
}
h4 {
    font-weight: 400;
    font-size: 24px;
}