.site-social {
    &._vertical {
        position: absolute;
        top: 0;
        right: 0;

        ul {
            li {
                display: block;
                margin: 0 0 20px 0;
            }
        }
        @include tablet {
            display: none;
        }
    }
    ul {
        padding: 0;
        margin: 0;
        list-style: none;

        li {
            vertical-align: middle;
            display: inline-block;
            margin-right: 20px;
            text-align: center;
            transition: $transition;

            a {
                transition: $transition;
            }
            &:hover {
                .icon {
                    fill: map_get($colors, default);
                    stroke: map_get($colors, default);
                }
            }
            .icon {
                max-width: 30px;
                max-height: 30px;
                fill: #000;
                stroke: #000;

                &-in {
                    max-width: 35px;
                    max-height: 35px;
                }
                &-fb {
                    max-width: 20px;
                    max-height: 20px;
                }

                @include desktop_hd {
                    max-width: 40px;
                    max-height: 40px;

                    &-in {
                        max-width: 45px;
                        max-height: 45px;
                    }
                    &-fb {
                        max-width: 25px;
                        max-height: 25px;
                    }
                }
            }
        }
    }
    @include tablet {
        margin: 0;
        width: 100%;
        text-align: center;

        ul {
            li {
                margin: 0 10px;
            }
        }
    }
    @include mobile {
        ul {
            li {
                margin: 0 5px;

                .icon {
                    max-width: 25px;
                    max-height: 25px;

                    &-in {
                        max-width: 30px;
                        max-height: 30px;
                    }
                    &-fb {
                        max-width: 18px;
                        max-height: 18px;
                    }
                }
            }
        }
    }
}
.social--in-right {
    position: relative;
    padding-right: 20%;

    @include tablet {
        padding-right: 0;
    }
}