.form {
    max-width: 500px;

    &._subscribe {
        display: flex;
        flex-wrap: wrap;
        align-items: center;

        .form {
            &-group {
                padding: 0;
                margin: 0;

                &._active {
                    .form {
                        &-placeholder {
                            top: 50%;
                            opacity: 0;
                        }
                    }
                }
            }
            &-control {
                font-size: 18px;
                height: 40px;
                line-height: 40px;
                border-radius: 30px;
                border: 1px solid #000;

                @include desktop_hd {
                    font-size: 20px;
                }
            }
            &-placeholder {
                top: 50%;
                font-size: 18px;
            }
            &-submit {
                font-size: 18px;
                margin: 0 0 0 20px;
                padding: 9px 40px 8px;

                @include desktop_hd {
                    font-size: 20px;
                }
            }
        }
        @include tablet {
            justify-content: center;
            margin: 0 auto;

            .form {
                &-group {
                    width: 100%;
                }
                &-control {
                    width: 100%;
                }
                &-submit {
                    margin-top: 20px;
                }
            }
        }
    }
    &-group {
        position: relative;
        padding-bottom: 20px;
        margin-bottom: 5px;

        &._active {
            .form {
                &-placeholder {
                    font-size: 13px;
                    top: 0;
                    opacity: .5;
                    @include desktop_hd {
                        font-size: 15px;
                    }
                }
            }
        }
        &._error {
            .form {
                &-control {
                    color: #D0021B;
                    border-color: #D0021B;
                }
                &-error {
                    opacity: 1;
                }
            }
        }
    }
    &-placeholder {
        position: absolute;
        top: calc(50% - 10px);
        left: 10px;
        font-size: 18px;
        color: #000;
        transition: $transition;
        pointer-events: none;
        transform: translate(0, -50%);

        span {
            color: #f00480;
        }
        @include desktop_hd {
            font-size: 22px;
        }
    }
    &-error {
        position: absolute;
        top: calc(100% - 15px);
        left: 5px;
        opacity: 1;
        font-size: 13px;
        color: #f00480;
        transition: $transition;
        pointer-events: none;
        opacity: 0;

        @include desktop_hd {
            font-size: 15px;
        }
    }
    &-control {
        width: 100%;
        height: 45px;
        line-height: 45px;
        padding: 0 10px;
        border: 0;
        border-bottom: 1px solid #000;
        outline: none;
        font-size: 16px;
        background: transparent;
        color: #000;
        transition: $transition;
        @include desktop_hd {
            font-size: 22px;
            height: 55px;
            line-height: 55px;
        }
    }
    &-submit {
        margin-top: 10px;
    }
    @include desktop_hd {
        max-width: 700px;
    }
}