.team {
    width: 100%;
    max-width: 1300px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;

    &._left {
        .team {
            &-thumb {
                order: 1;
            }
            &-content {
                order: 2;
                margin-left: auto;
            }
        }
    }
    &._right {
        .team {
            &-thumb {
                order: 2;

                @include tablet {
                    order: 1;
                }
            }
            &-content {
                order: 1;
                margin-right: auto;

                @include tablet {
                    order: 2;
                }
            }
        }
    }
    &:not(:last-child) {
        margin-bottom: 100px;

        @include desktop_hd {
            margin-bottom: 165px;
        }
        @include tablet {
            margin-bottom: 50px;
        }
        @include media-max(480px) {
            margin-bottom: 30px;
        }
    }
    &-thumb {
        min-height: 580px;
        width: 100%;
        max-width: calc(50% - 80px);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;

        @include desktop_hd {
            min-height: 700px;
            max-width: calc(50% - 130px);
        }
        @include tablet {
            max-width: 100%;
            background-position: center 20%;
            min-height: 800px;
            position: relative;

            &:before {
                content: '';
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
                height: 230px;

                background: -moz-linear-gradient(top, rgba(242,242,242,0.01) 0%, rgba(242,242,242,1) 100%);
                background: -webkit-linear-gradient(top, rgba(242,242,242,0.01) 0%,rgba(242,242,242,1) 100%);
                background: linear-gradient(to bottom, rgba(242,242,242,0.01) 0%,rgba(242,242,242,1) 100%);
                filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#03f2f2f2', endColorstr='#f2f2f2',GradientType=0 );

                @include media-max(480px) {
                    height: 130px;
                }
            }
        }
        @include media-max(480px) {
            min-height: 350px;
        }
    }
    &-content {
        width: 50%;

        @include tablet {
            width: 100%;
            margin-top: -230px;
            background: rgba(#F2F2F2, .5);
            padding: 30px;
            position: relative;
            z-index: 2;
        }
        @include media-max(480px) {
            margin-top: -130px;
            padding: 30px 20px;
        }
    }
    &-title {
        font-size: 24px;
        font-weight: 500;
        margin: 0;
        text-transform: uppercase;
        color: #f00480;
        margin-bottom: 5px;

        @include desktop_hd {
            font-size: 35px;
        }
        @include mobile {
            font-size: 20px;
        }
    }
    &-role {
        font-size: 18px;
        margin-bottom: 10px;
        font-weight: 300;
        display: block;

        @include desktop_hd {
            font-size: 21px;
        }
        @include mobile {
            font-size: 16px;
        }
    }
    &-line {
        display: block;
        height: 5px;
        max-width: 135px;
        background: #000;
        margin-bottom: 45px;

        @include desktop_hd {
            margin-bottom: 55px;
        }
    }
    &-quote {
        display: block;
        font-size: 22px;
        font-weight: 400;
        margin-bottom: 45px !important;

        @include desktop_hd {
            font-size: 26px;
            margin-bottom: 55px !important;
        }
        @include mobile {
            font-size: 16px;
        }
    }
    &-info {
        font-size: 18px;
        font-weight: 300;
        display: block;

        @include desktop_hd {
            font-size: 21px;
        }
        @include mobile {
            font-size: 16px;
        }
    }
}