.faq {
    margin: 0 0 30px 0;

    &-question {
        font-size: 22px;
        margin: 0;
        text-transform: uppercase;
        cursor: pointer;
        transition: $transition;

        &:hover {
            color: map_get($colors, default);
        }

        @include desktop_hd {
            font-size: 30px;
        }
    }
    &-answer {
        padding: 10px 0 0 0;
        display: none;

        p,
        ul li {
            font-size: 18px;

            @include desktop_hd {
                font-size: 24px;
            }
            @include mobile {
                font-size: 16px;
                line-height: 1.3;
            }
        }
    }
    @include desktop_hd {
        margin-bottom: 50px;
    }
    @include mobile {
        margin-bottom: 20px;

        &-question {
            font-size: 18px;
        }
    }
}