.site-footer {
    padding: 25px 0;
    margin-top: auto;

    &._shadow {
        position: relative;

        &:before {
            content: '';
            position: absolute;
            bottom: 100%;
            left: 0;
            right: 0;
            height: 10px;
            background: url('../shadow-footer.png') no-repeat;
            background-size: auto 110px;
            background-position: center 0;
        }
    }
    .copyright {
        margin-left: auto;
        font-size: 20px;
        font-weight: 300;

        a {
            text-decoration: underline;
        }
        @include desktop_hd {
            font-size: 24px;
            line-height: 1.3;
        }
        @include tablet {
            margin: 20px 0 0;
            width: 100%;
            text-align: center;
        }
        @include mobile {
            font-size: 16px;
        }
    }
    .language {
        display: none;
    }
    .site-nav {
        width: 100%;
        margin-bottom: 25px;
        padding-bottom: 25px;
        border-bottom: 1px solid #000;

        ul {
            li {
                margin-right: 30px;
                margin-left: 0;
            }
            @include tablet {
                columns: 2;
                text-align: center;

                li {
                    margin-bottom: 5px;
                    width: 100%;
                    display: block;
                }
            }
        }
        @include tablet {
            width: calc(100% + 40px);
            margin-left: -20px;
            margin-right: -20px;
            margin-bottom: 20px;
        }
    }
    @include media-max(1400px) {
        padding: 20px 0;
    }
    @include tablet {
        padding: 15px 0 20px;
    }
}