.icon {
    transition: $transition;

    &-menu {
        display: block;
        cursor: pointer;

        &__bar {
            display: block;
            width: 22px;
            height: 2px;
            background: rgba(#000, .8);

            &:not(:last-child) {
                margin-bottom: 4px;
            }
            &:last-child {
                width: 12px;
                margin-left: auto;
            }
        }
        @include media-min(992px) {
            display: none;
        }
    }
    &-cross {
        max-width: 25px;
        max-height: 25px;
        fill: #E3147E;
        position: absolute;
        top: 20px;
        right: 20px;
        opacity: 1;
        cursor: pointer;

        &:hover {
            fill: darken(#E3147E, 30%);
        }
    }
    &-svg {
        max-width: 100%;
        height: auto;

        &-profit {
            width: 100%;

            @include desktop_hd {
                width: 83%;
            }
        }
    }
}