.info {
    padding: 120px 0;

    &._contacts {
        .social--in-right {
            padding-right: 5%;
            @include tablet {
                padding-right: 0;
            }
        }

        .info {
            &-right {
                max-width: 300px;
                margin-left: auto;

                a {
                    font-size: 20px;
                    font-weight: 300;
                    @include desktop_hd {
                        font-size: 24px;
                        line-height: 1.3;
                    }
                    @include mobile {
                        font-size: 16px;
                    }
                }
                @include tablet {
                    max-width: 100%;
                    margin-bottom: 30px;
                }
                @include mobile {
                    margin-bottom: 0;
                }
            }

            &-contact {
                margin-bottom: 40px;
                @include mobile {
                    margin-bottom: 20px;
                }
            }
        }

        h4 {
            font-size: 24px;
            margin-bottom: 5px;
            @include mobile {
                font-size: 20px;
                margin-bottom: 0;
            }
            @include desktop_hd {
                font-size: 30px;
                line-height: 1.3;
            }
        }
    }

    &._about {
        &-banner {
            padding: 0 !important;
            position: relative;

            .container {
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
            }
            .site-social._vertical {
                top: 50%;
                right: 20px;
                transform: translate(0, -50%);

                @include desktop_hd {
                    right: 0;
                }
            }
        }
        &-description {
            .container {
                max-width: 920px;
                margin: 0 auto;

                @include desktop_hd {
                    max-width: 1320px;
                    padding-left: 150px;
                }
            }
            .info {
                &-left {
                    width: 25%;

                    h1 {
                        font-weight: 400;
                        font-size: 50px;

                        @include desktop_hd {
                            font-size: 86px;
                        }
                    }
                    @include tablet {
                        width: 100%;
                        text-align: center;

                        h1 {
                            display: inline-block;
                            margin-bottom: 40px;
                            padding-bottom: 10px;
                            position: relative;

                            &:before {
                                content: '';
                                position: absolute;
                                top: 100%;
                                left: 0;
                                right: 0;
                                height: 5px;
                                background: #000;
                            }
                            @include mobile {
                                font-size: 40px;
                            }
                        }
                    }
                }
                &-right {
                    width: 75%;

                    p {
                        font-size: 18px;

                        @include desktop_hd {
                            font-size: 22px;
                        }
                        @include mobile {
                            font-size: 16px;
                        }
                    }
                    @include desktop_hd {
                        padding-left: 50px;
                    }
                    @include tablet {
                        width: 100%;
                    }
                }
            }
        }
        &-team {
            padding-top: 0 !important;
        }
    }

    &._info-efficiency {
        padding: 35px 0 90px;

        @include mobile {
            padding: 40px 0;
        }
    }

    &._info-x3 {
        max-height: 665px;

        &._landing {
            padding-top: 60px;
            padding-bottom: 60px;

            .info-left {
                margin-top: -90px;

                img {
                    width: 160%;
                    max-width: none;
                    margin-left: -170px;
                }
                @include desktop_hd {
                    margin-top: -150px;
                    margin-left: -90px;

                    img {
                        width: 120%;
                        max-width: none;
                        margin-left: -100px;
                    }
                }
                @include tablet {
                    width: 100%;
                    margin: 0 0 30px;

                    img {
                        margin: 0;
                        width: auto;
                        max-width: 100%;
                    }
                }
            }
            .info-right {
                @include tablet {
                    width: 100%;
                    max-width: 100%;

                    h1,
                    p {
                        text-align: center;
                    }
                }
            }
            br {
                display: none;
            }
            @include desktop_hd {
                padding-top: 120px;

                br {
                    display: block;
                }
            }
        }

        .social--in-right {
            padding-right: 120px;
            @include tablet {
                padding-right: 0;
            }
        }

        .info {
            &-left {
                padding-right: 40px;
                margin: -150px 0 -220px;
                width: calc(100% - 500px);

                img {
                    max-height: 700px;
                    @include desktop_hd {
                        max-height: 780px;
                    }
                }
            }

            &-right {
                width: 100%;
                max-width: 400px;
                margin-left: auto;
            }
            @include desktop_hd {
                &-left {
                    width: calc(100% - 510px);
                }
                &-right {
                    max-width: 510px;
                }
            }
            @include desktop {
                &-left {
                    margin: -110px 0 -170px;
                    width: 60%;
                }

                &-right {
                    max-width: 40%;
                }
            }
            @include tablet {
                &-left {
                    margin: -80px 0 -120px -40px;
                    width: 60%;
                }

                &-right {
                    max-width: 40%;
                }
            }
            @include mobile {
                &-left {
                    margin: 0;
                    width: 100%;
                    text-align: center;

                    img {
                        max-height: 300px;
                    }
                }

                &-right {
                    max-width: 100%;
                    text-align: center;
                }
            }
        }

        h1 {
            font-size: 70px;
            font-weight: 600;
            font-family: "ProximaNova";
            color: rgb(0, 0, 0);
            line-height: 1.059;
            text-align: right;
            text-shadow: 0 9px 2.79px rgba(0, 0, 0, 0.21);
            margin: 0 0 30px;
            @include desktop_hd {
                font-size: 90px;
            }
            @include desktop {
                font-size: 50px;
            }
            @include mobile {
                text-align: center;
                text-shadow: 0 5px 2.79px rgba(0, 0, 0, 0.21);
                font-size: 30px;
            }
        }

        p {
            font-size: 18px;
            font-weight: 400;
            line-height: 1.3;
            @include desktop_hd {
                font-size: 24px;
            }
            @include desktop {
                font-size: 20px;
            }
            @include mobile {
                font-size: 16px;
                line-height: 1.3;
            }
        }
        @include desktop {
            max-height: none;
        }
    }

    &._info-spetification {
        ul {
            @extend .list__links;
        }
    }

    &._slider-landing {
        @include desktop_hd {
            padding: 200px 0;
        }
    }

    &-content {
        width: 100%;
    }

    &-left {
        width: 50%;
    }

    &-right {
        width: 50%;
    }

    &-title {
        font-size: 40px;
        font-weight: 400;
        color: rgb(3, 3, 3);
        line-height: 1;
        text-align: center;
        text-shadow: 0 9px 2.79px rgba(0, 0, 0, 0.21);
        margin-bottom: 60px;

        &._text {
            &-center {
                text-align: center;
            }
            &-left {
                text-align: left;
            }
        }
        @include desktop_hd {
            font-size: 56px;
            margin-bottom: 80px;

            &._big {
                font-size: 100px;
            }
        }
        @include tablet {
            font-size: 30px;
            margin-bottom: 40px;
        }
        @include mobile {
            font-size: 24px;
            text-shadow: 0 5px 2.79px rgba(0, 0, 0, 0.21);
            margin-bottom: 30px;
        }
    }

    ul {
        padding: 0 0 0 22px;
        margin: 0;

        li {
            font-size: 18px;
            margin: 0;
            font-weight: 300;
            line-height: 1.4;
            color: map_get($colors, text);

            &:not(:last-child) {
                margin-bottom: 5px;
            }
            @include desktop_hd {
                font-size: 24px;
                line-height: 1.3;
            }
            @include mobile {
                font-size: 16px;
                line-height: 1.3;
            }
        }
    }

    @include desktop {
        padding: 80px 0;
    }
    @include tablet {
        padding: 60px 0;

        &-left,
        &-right {
            width: 100%;
        }
    }
    @include mobile {
        padding: 40px 0;
    }
}