*,
*:after,
*:before {
    box-sizing: border-box;
}

body,
html {
    font-family: map_get($fonts, default);
    font-size: 62.5%;
    background: map_get($bgColors, default);
}

body {
    line-height: 1.5;
}

address {
    font-style: normal;
}

img {
    height: auto;
    max-width: 100%;
}

label {
    cursor: pointer;
    user-select: none;
}

textarea {
    resize: vertical;
}

strong {
    font-weight: bold;
}
@include placeholder {
    text-overflow: ellipsis;
}

input[placeholder] {
    text-overflow: ellipsis;
}

input::-moz-placeholder {
    text-overflow: ellipsis;
}

input:-moz-placeholder {
    text-overflow: ellipsis;
}

input:-ms-input-placeholder {
    text-overflow: ellipsis;
}

:focus::-webkit-input-placeholder {
    color: transparent;
}

:focus::-moz-placeholder {
    color: transparent;
}

:focus:-moz-placeholder {
    color: transparent;
}

:focus:-ms-input-placeholder {
    color: transparent;
}

.container {
    margin-left: auto;
    margin-right: auto;
    padding: 0 15px;
    width: 100%;
    max-width: 1250px;

    &._mini {
        max-width: 960px;
    }
    @include desktop_hd {
        max-width: 1750px;
    }
    @include tablet {
        padding: 0 20px;
    }
}

.site {
    &-wrapper {
        display: flex;
        flex-direction: column;
        font-size: 18px;
        line-height: normal;
        min-height: 100vh;
        overflow: hidden;
        min-width: 300px;

        &:before {
            content: '';
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: rgba(#E3147E, .3);
            pointer-events: none;
            z-index: 200;
            transition: $transition;
            opacity: 0;
        }
    }
    &-nav {
        &--additional {
            padding: 25px 0;
            text-align: right;

            &._landing {
                @include tablet {
                    display: none;
                }
            }
            ul {
                list-style: none;
                padding: 0;
                margin: 0;

                li {
                    display: inline-block;
                    vertical-align: middle;
                    margin-left: 35px;

                    &._active a:not(.button),
                    &:hover a:not(.button) {
                        color: map_get($colors, linkActive);
                    }
                    a:not(.button) {
                        color: #000;
                        font-size: 20px;
                        font-weight: 300;

                        @include desktop_hd {
                            font-size: 26px;
                        }
                        @include tablet {
                            font-size: 18px;
                        }
                    }
                    .button {
                        font-size: 18px;

                        @include desktop_hd {
                            font-size: 22px;
                        }
                        @include tablet {
                            font-size: 16px;
                        }
                    }
                    @include desktop_hd {
                        margin-left: 45px;
                    }
                    @include tablet {
                        margin-left: 20px;
                    }
                }
            }
            .button {
                margin-left: 50px;

                @include desktop_hd {
                    margin-left: 80px;
                }
                @include desktop {
                    margin-left: 40px;
                }
            }
            @include desktop_hd {
                padding: 40px 0;
            }
            @include tablet {
                padding: 15px 0;
            }
            @include mobile {
                display: none;
            }
        }
        ul {
            list-style: none;
            padding: 0;
            margin: 0;

            li {
                display: inline-block;
                color: #000;
                margin-left: 30px;
                font-weight: 300;

                &._active {
                    font-weight: 700;
                }
                &._active,
                &:hover {
                    color: map_get($colors, linkActive);
                }
                a {
                    color: inherit;
                }
                @include desktop_hd {
                    font-size: 26px;
                    margin-left: 55px;
                }
                @include media-max(1400px) {
                    font-size: 16px;
                }
            }
        }
    }
    &-logo {
        max-width: 110px;

        @include desktop_hd {
            max-width: 170px;
        }
        @include tablet {
            img {
                max-height: 45px;
            }
        }
        @include mobile {
            img {
                max-height: 35px;
            }
        }
    }
    &-mobile {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        max-width: 70%;
        padding: 80px 20px 50px 80px;
        background: #fff;
        overflow-y: auto;
        overflow-x: hidden;
        transition: $transition;
        pointer-events: none;
        transform: translate(100%, 0);
        z-index: 201;

        &._active {
            transform: translate(0, 0);
            pointer-events: all;
        }
        .site {
            &-nav {
                li {
                    display: block;
                    margin-left: 0;
                    font-size: 24px;
                    color: #000;
                    margin-bottom: 20px;
                }
            }
        }
        @include media-min(992px) {
            display: none;
        }
        @include media-max(480px) {
            max-width: 100%;
            padding: 100px 20px 50px;
        }
    }
}

.language {
    margin-left: 50px !important;

    @include desktop_hd {
        margin-left: 100px !important;
    }
    @include desktop {
        margin-left: 40px !important;
    }
    @include tablet {
        margin-left: 0 !important;
    }
}

._flex {
    display: flex;
    flex-wrap: wrap;

    &--inline {
        display: inline-flex;
    }

    &--items {
        &-center {
            align-items: center;
        }
        &-end {
            align-items: flex-end;
        }
        &-start {
            align-items: flex-start;
        }
    }

    &--swap {
        &-xs {
            @include mobile {
                flex-wrap: wrap-reverse;
                flex-direction: row-reverse;
            }
        }

        &-sm {
            @include tablet {
                flex-wrap: wrap-reverse;
                flex-direction: row-reverse;
            }
        }

        &-md {
            @include desktop {
                flex-wrap: wrap-reverse;
                flex-direction: row-reverse;
            }
        }
    }
}

._no-scroll {
    overflow: hidden;
}

._bg {
    @each $bgColorName, $bgColorValue in $bgColors {
        &_#{$bgColorName} {
            background-color: $bgColorValue;
        }
    }
}

._text {
    &-center {
        text-align: center;
    }
    &-left {
        text-align: left;
    }
    &-semibold {
        font-weight: 500 !important;
    }
}

._event-menu-opened {
    .site-wrapper:before {
        pointer-events: all;
        opacity: 1;
    }
}