.mmm {
    &-container {
        max-width: 1500px;
        margin: 0 auto;
        position: relative;

        @include mobile {
            display: flex;
            flex-wrap: wrap;
            align-items: flex-start;
            margin: -30px -20px 0;

            .list__links {
                margin-left: 20px !important;
                margin-right: 20px !important;
            }
        }
    }
    &-image {
        display: block;
        margin: 0 auto;

        &--container {
            position: relative;
            width: 50%;
            margin: 0 auto;
        }
        @include media-min(992px) {
            transform: scale(1.2);
        }
        @include mobile {
            width: auto;
            margin-top: 30px;
            margin-bottom: 30px;

            &--container {
                width: auto;
            }
        }
    }
    &-item {
        position: absolute;
        font-size: 22px;
        transition: $transition;

        &:before {
            content: '';
            position: absolute;
            top: calc(100% + 2px);
            left: 0;
            right: 0;
            height: 3px;
            background: #000;
            transition: $transition;
        }
        &._focused {
            color: #FD28F9;

            &:before {
                background: #FD28F9;
            }
        }
        &._1 {
            top: 20%;
            left: 0;
        }
        &._2 {
            top: 40%;
            left: 0;
        }
        &._3 {
            top: 60%;
            left: 0;
        }
        &._4 {
            left: 80%;
            top: 20%;
        }
        &._5 {
            left: 80%;
            top: 40%;
        }
        &._6 {
            left: 80%;
            top: 60%;
        }
        @include desktop_hd {
            font-size: 26px;
        }
        @include desktop {
            font-size: 18px;
        }
        @include tablet {
            font-size: 16px;
        }
        @include mobile {
            width: calc(50% - 40px);
            display: inline-block;
            position: relative;
            left: inherit !important;
            bottom: inherit !important;
            top: inherit !important;
            margin: 30px 20px 0;
        }
    }
    &-dotted {
        position: absolute;
        z-index: 10;
        width: 5px;
        height: 5px;
        border-radius: 50%;
        background: #FD28F9;
        box-shadow: 0 0 4px 1px rgba(253, 40, 249, 1), 0 0 10px 3px rgba(253, 40, 249, .6), 0 0 10px 5px rgba(253, 40, 249, .3), 0 0 30px 8px rgba(253, 40, 249, .9);
        transition: $transition;
        cursor: pointer;

        &:hover {
            transform: scale(1.5);
        }
        &._1 {
            top: 34%;
            left: 32.5%;
        }
        &._2 {
            top: 45%;
            left: 40.5%;
        }
        &._3 {
            top: 33%;
            left: 59%;
        }
        &._4 {
            top: 65%;
            left: 45.5%;
        }
        &._5 {
            top: 79.5%;
            left: 42%;
        }
        &._6 {
            top: 67%;
            left: 62%;
        }
    }
}