.features {
    padding: 0 !important;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    margin: -30px -15px 0 !important;

    &-icon {
        height: 95px;
        margin-bottom: 20px;
        display: flex;
        align-items: center;
        justify-content: center;

        @include desktop_hd {
            margin-bottom: 30px;
        }
        @include desktop {
            max-width: 70px;
            margin: 0 auto;
        }
    }
    li {
        margin: 30px 15px 0 !important;
        width: calc(25% - 30px);
        text-align: center;
        position: relative;
        color: rgb(3, 3, 3);

        p {
            font-size: 24px;
            font-weight: 400;

            @include desktop_hd {
                font-size: 34px;
            }
            @include desktop {
                font-size: 20px;
            }
            @include tablet {
                font-size: 18px;
            }
        }
        @include tablet {
            width: calc(50% - 30px);
        }
        @include mobile {
            width: calc(100% - 30px);
        }
    }
}