.slider {
    padding: 60px;

    .info-title {
        margin-bottom: 60px;

        @include desktop {
            margin-bottom: 50px;
        }
        @include tablet {
            margin-bottom: 40px;
        }
        @include mobile {
            margin-bottom: 30px;
        }
    }
    ._slide1 {
        .info-title {
            @include desktop_hd {
                font-size: 75px;
            }
        }
        .slider-inside {
            max-width: 1500px;
            margin: 0 auto;
        }
        ul {
            li {
                @include desktop_hd {
                    font-size: 33px;
                }
            }
        }
    }
    ._slide2 {
        .info-title {
            @include mobile {
                text-align: center;
            }
        }
    }
    ._slide4 {
        .info-title {
            padding-left: 130px;

            @include desktop_hd {
                padding-left: 270px;
            }
            @include desktop {
                padding-left: 0;
            }
        }
    }
    .slick {
        &-arrow {
            position: absolute;
            z-index: 50;
            top: 0;
            width: 33px;
            height: 15px;
            border: 0;
            background-color: transparent;
            background-repeat: no-repeat;
            background-size: cover;
            border-radius: 0;
            font-size: 0;
            cursor: pointer;
        }
        &-next {
            right: 0;
            background-image: url('../arrow-right.png');
        }
        &-prev {
            right: 72px;
            background-image: url('../arrow-left.png');
        }
        &-slides {
            position: absolute;
            top: 50%;
            right: 0;
            transform: translate(0, -50%);

            &__current {
                padding-bottom: 80px;
                position: relative;

                &:before {
                    content: '';
                    position: absolute;
                    bottom: 10px;
                    width: 2px;
                    height: 60px;
                    background: #000;
                    left: 50%;
                    transform: translate(-50%, 0);
                }
                @include desktop_hd {
                    padding-bottom: 120px;

                    &:before {
                        height: 100px;
                    }
                }
            }
            &__current,
            &__summary {
                display: block;
                font-size: 24px;

                @include desktop_hd {
                    font-size: 32px;
                }
                @include desktop {
                    font-size: 20px;
                }
                @include tablet {
                    font-size: 18px;
                }
            }
            @include tablet {
                display: none;
            }
        }
    }
    .features {
        @include media-min(992px) {
            li {
                width: calc(50% - 30px);
                display: flex;
                align-items: center;
                margin-top: 60px !important;
            }
            &-icon {
                width: 120px;
                margin-bottom: 0;
                margin-right: 30px;
            }
        }
    }
    @include tablet {
        padding: 40px 0 0 0;

        .info-left {
            margin-bottom: 40px;
        }
    }
}