.list {
    &__links {
        padding: 0 !important;
        margin-bottom: 0 !important;
        list-style: none;
        margin-top: 50px;

        li {
            display: inline-block;
            margin-right: 40px;
            font-size: 24px;

            a {
                color: #000;

                &:hover {
                    color: map_get($colors, default);
                }
                span {
                    color: map_get($colors, default);
                }
            }
            @include desktop_hd {
                font-size: 29px;
                margin-right: 70px;
            }
            @include tablet {
                margin-right: 30px;
                font-size: 18px;
            }
            @include mobile {
                margin-right: 20px;
                font-size: 16px;
            }
        }
        @include desktop_hd {
            margin-top: 80px;
        }
        @include tablet {
            margin-top: 40px;
        }
        @include mobile {
            margin-top: 30px;
        }
    }
    &__style {
        padding: 0 !important;
        list-style: none;

        li {
            position: relative;
            padding-left: 22px;

            &:before {
                content: '';
                position: absolute;
                top: 9px;
                left: 0;
                width: 6px;
                height: 6px;
                background: #ED2A8D;
                border-radius: 50%;

                @include desktop_hd {
                    top: 12px;
                    width: 8px;
                    height: 8px;
                }
                @include mobile {
                    top: 7px;
                }
            }
            &:not(:last-child) {
                margin-bottom: 10px !important;
            }
        }
    }
}